<template>
  <div id="oxygen" class="container-fluid">
    <div class="table-responsive">
      <table class="table table-hover align-middle table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col" > <a href="#" v-on:click="sort('address')">Address</a> </th>
            <th scope="col"> <a href="#" v-on:click="sort('contactinformation')">Contact</a> </th>
            <th scope="col"> <a href="#" v-on:click="sort('quantityavailable')">Quantity Available</a> </th>
            <th scope="col"> <a href="#" v-on:click="sort('price')">Price</a> </th>
            <th scope="col"> <a href="#" v-on:click="sort('lastupdatedat')"> Last Updated At</a> </th>
            <th scope="col"><a href="#" v-on:click="sort('details')"> Remarks </a> </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(quote, index) in showOxygenList" :key="quote.id" class="organisation-name">
            <th scope="row">{{index+1}}</th>
            <td>{{ quote.address }}</td>
            <td><a :href="'tel:'+ quote.contactinformation">{{ quote.contactinformation }}</a> <br/> {{ quote.name }}</td>
            <td>{{ quote.quantityavailable }}</td>
            <td>{{ quote.price }}</td>
            <td>{{ quote.lastupdatedat }}</td>
            <td>{{ quote.details }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    Last updated at: {{sheetUpdated.$t}}
    <hr/>
      Visitor
    <h2>{{ count }} </h2>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Oxygen",
  props: {
    msg: String,
  },
  data() {
    return {
      gsheet_url:
        "https://spreadsheets.google.com/feeds/list/1FOu1EFIudho88Iz5sHNgrr8XWSzJkdi9Grxty2U0Rz4/1/public/values?alt=json",
      oxygenList: [],
      sheetUpdated: '',
      count: 0,
      currentSort: 'lastupdatedat',
      currentSortDir: 'asc',
      search: '',
    };
  },
  computed: {
    showOxygenList() {
      return this.oxygenList.filter(a => {
        // console.log('a.pn',a.address)
        return (a.address + '').includes(this.search)
      })
      .sort((a, b) => {
      if (this.currentSortDir === 'asc') {
        return a[this.currentSort] >= b[this.currentSort];      
      }
      return a[this.currentSort] <= b[this.currentSort];
    })
    },
  },
  created: function () {
    
    axios.get(this.gsheet_url).then((res) => {
        // this.oxygenList = res.data;
        // console.log("gsheet", res.data.feed.entry);
        this.sheetUpdated = res.data.feed.updated;
        this.parseData(res.data.feed.entry);
    });
    this.updateVisitCount();

  },
  methods: {
    parseData: function (entries) {
      // console.log("entries", entries);

      entries.forEach( (value) => {
          var entry = {
              "name": value.gsx$name.$t,
              "address": value.gsx$address.$t,
              "contactinformation": value.gsx$contactinformation.$t,
              "quantityavailable": value.gsx$quantityavailable.$t,
              "price": value.gsx$price.$t,
              "lastupdatedat": value.gsx$lastupdatedat.$t,
              "lastupdatedby": value.gsx$lastupdatedby.$t,
              "details": value.gsx$details.$t,
              "status": value.gsx$status.$t
          };
          if(value.gsx$status.$t === 'Available'){
            // Push entry into the list of quotes
            this.oxygenList.push(entry);
          }
      });
      // this.authorList = Array.from(authorSet);
      // this.authorList.sort();
      // this.tagList = Array.from(tagSet);
      // this.tagList.sort();
    },
    sort: function(col) {
      // if you click the same label twice
      console.log('col', col)
      if(this.currentSort == col){
        this.currentSortDir = this.currentSortDir === 'asc' ? 'desc' : 'asc';
      }else{
        this.currentSort = col;
        console.log( 'diff col: '+col );
      } // end if

    }, // sort
    updateVisitCount: function () {
      axios.get('https://api.countapi.xyz/hit/cov-aid/kolkata-oxygen').then((res) => {
          console.log("hit", res.data);
          this.count = res.data.value;
      });
    },
    LocalVisitCount: function () {
      var n = localStorage.getItem('oxygen_visit_counter');
      if (n === null) {
          n = 0;
      }
      n++;
      localStorage.setItem("oxygen_visit_counter", n);
      this.count = n;
    }
  },
};
</script>
<style>
table thead {white-space:nowrap}
.table th a {
  position: relative;
  z-index: 9;
}
.table-hover tbody tr:hover td, .table-hover tbody tr:hover th {
  background-color: #9bc8b4;
}
</style>
